.top {
    display: flex;
    justify-content: space-between;
    padding: 1rem;

}

.left {
    width: 50%;
    color: aqua;
}

.right {
    display: flex;
    justify-content: end;
    width: 50%;
    align-items: center;

}

.right>p {
    font-size: 24px;
    font-weight: bolder;
}

.outer {
    margin: 0 1rem;
    width: 100px;
    height: 50px;
    border-radius: 1rem;
    background-color: black;
    display: flex;
    justify-content: start;
    align-items: center;
}

.inner {
    margin: 0.4rem;
    width: 50%;
    background-color: white;
    height: 80%;
    border-radius: 50%;
}

ul {
    margin: 5rem;
    font-size: 35px;
    list-style: circle;
}

ul>li {
    padding: 0.5rem 0;
}

.main>h1 {
    margin: 3rem 2rem;
    font-size: 50px;
}

.dark-top {
    background-color: black;
}

.dark-outer {
    background-color: white;
    display: flex;
    justify-content: end;
}

.dark-inner {
    background-color: black;
}

.dark-right>p {
    color: white;
}

.dark-main {
    color: white;
}

.dark-html {
    background-color: black;
}